import listSource from "@/assets/homeStayList.json";

export default {
  test() {
    console.log(listSource);
  },
  getNavList() {
    const source = listSource.homeStay;
    let navList = [];

    source.forEach((item, index) => {
      let navDistributionIndex = navList.findIndex((nav) => {
        return nav.distribution === item.distribution;
      });

      if (navDistributionIndex === -1) {
        navList.push({
          distribution: item.distribution,
          navLabels: [],
        });

        navDistributionIndex = navList.length - 1;
      }

      navList[navDistributionIndex].navLabels.push({
        navName: item.location + " - " + item.name,
        navIndex: index,
      });
    });

    return navList;
  },
  getHomeStayInfo(index) {
    const source = listSource.homeStay;

    return source[index];
  },
};
